import { useState } from "react"
import { api } from '../config/api';

export const useData = () => {

    const [data, setData] = useState(null)
    const [filter, setFilter] = useState([])
    const [filename, setFilename] = useState(null)
    const [loading, setLoading] = useState(false)

    const filterHandler = async (newFilter, action = 'plus') => {

        let newFilters = []

        if (action === 'plus') {
            const newFiltersObject = {
                column: newFilter[0].split(':')[0],
                value: newFilter.map(item => item.split(':')[1])
            }
            newFilters = [...filter, newFiltersObject]
        } else {
            const isExistedFilterHasOneValue = filter[newFilter.index].value.length === 1 ? true : false
            if (isExistedFilterHasOneValue) {
                newFilters = filter.filter(item => item.column !== newFilter.column)
            } else {
                const tempFilters = filter.map((item) => item.column === newFilter.column ? ({ ...item, value: item.value.filter(valueItem => valueItem !== newFilter.value) }) : item)
                newFilters = tempFilters
            }
        }

        setFilter(newFilters)
        setLoading(true)
        const result = await api.setFilter(filename, newFilters)
        setData(result.data)
        setLoading(false)
    }

    const getDataByFilename = async (filename) => {
        setLoading(true)
        const result = await api.getFileByFilename(filename)
        setData(result.data)
        setFilename(filename)
        setLoading(false)
    }

    return { data, setData, loading, filter, filterHandler, filename, setFilename, getDataByFilename }

}